.projects-container {
  height: 79%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.project-card {
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-info-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 40%;
}

.card-image {
  display: none;
}

.card-title, .card-resume,
.card-techs-title {
  margin: 1.5vh 0 0 0 ;
}

.card-title {
  font-size: 3vh;
  text-align: center;
}

.card-resume {
  font-size: 2.6vh;
  text-align: justify;
  width: 95%;
}

.card-techs-title {
  font-size: 3vh;
  text-align: center;
  width: 100%;
}

.card-ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items:center;
}

.card-li {
  text-align: center;
  height: 50%;
  width: 40%;
}

.card-link-container {
  margin-top: 3.5vh;
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-link {
  text-align: center;
  text-decoration: none;
  color: white;
  font-size: 2.6vh;
}

@media screen and (min-width: 500px) and (min-height: 600px) {
  .divisor {
    height: 100%;
    width: 0.3vh;
    background-color: white;
  }
  

  .projects-container {
    flex-direction: row;
  }
  
  .project-card {
    width: 50%;
    height: 100%;
    justify-content: center;
  }
  
  .card-title {
    font-size: 4vh;
    margin-bottom: 3vh;
  }

  .card-resume {
    text-align: justify;
    height: 60%;
    width: 80%;
  }

  .card-info-container {
    height: 30%;
  }
  
  .card-link-container {
    margin: 0;
  }

  .card-link {
    transition: 2s;
  }

  .card-link:hover{
    color: #08a6f0;
    font-size: 4vh;
  }
}
